
import { computed, defineComponent, onMounted, ref } from "vue";
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonButtons,
  IonBackButton,
  IonTitle,
  IonContent,
  IonCard,
  loadingController,
  alertController,
} from "@ionic/vue";
import NeopleApi from "@/utils/NeopleApi";
import { useRoute } from "vue-router";
import Common from "@/utils/Common";
import NoData from "@/components/NoData.vue";

export default defineComponent({
  components: {
    IonPage,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonBackButton,
    IonTitle,
    IonContent,
    IonCard,
    NoData,
  },
  setup() {
    const gameInfo = ref();

    const route = useRoute();

    const randomText = (random: boolean) => (random ? "랜덤" : "선택");

    const players = computed(() => {
      let result = [] as any[];

      if (!Common.isNull(gameInfo.value)) {
        (gameInfo.value.teams as any[]).forEach((team) => {
          (team.players as string[]).forEach((playerId) => {
            result.push({
              gameResult: team.result,
              ...(gameInfo.value.players as any[]).filter(
                (info) => info.playerId === playerId
              )[0],
            });
          });
        });
      }

      return result.sort((a) => (a.gameResult === "win" ? -1 : 1));
    });

    onMounted(async () => {
      const loading = await loadingController.create({
        message: "데이터 조회중",
        mode: "ios",
      });
      loading.present();

      try {
        gameInfo.value = await NeopleApi.cyPlayerMatchesInfo({
          matchId: route.params.matchId as string,
        });
      } catch (error) {
        const alert = await alertController.create({
          header: "오류 발생",
          subHeader: `${error}`,
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } finally {
        loading.dismiss();
      }
    });

    return {
      gameInfo,
      NeopleApi,
      randomText,
      players,
    };
  },
});
