
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonSearchbar,
  IonButtons,
  IonMenu,
  IonList,
  IonItem,
  IonMenuButton,
  menuController,
  onIonViewDidLeave,
  IonButton,
  IonIcon,
  loadingController,
  IonLabel,
  IonGrid,
  IonRow,
  IonCol,
  IonText,
  IonNote,
  IonFooter,
  alertController,
  IonListHeader,
  IonSegment,
  IonSegmentButton,
  IonThumbnail,
} from "@ionic/vue";
import { computed, defineComponent, ref, watch } from "vue";
import { searchSharp } from "ionicons/icons";
import NeopleApi from "@/utils/NeopleApi";
import Common from "@/utils/Common";
import NoData from "@/components/NoData.vue";

export default defineComponent({
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonSearchbar,
    IonButtons,
    IonMenu,
    IonList,
    IonItem,
    IonMenuButton,
    IonButton,
    IonIcon,
    IonLabel,
    IonGrid,
    IonRow,
    IonCol,
    IonText,
    IonNote,
    IonFooter,
    IonListHeader,
    IonSegment,
    IonSegmentButton,
    IonThumbnail,
    NoData,
  },
  setup() {
    const player = ref("");
    const playerId = ref("");
    const playerInfo = ref();
    const gameTypeId = ref();

    const playerSearch = async () => {
      const loading = await loadingController.create({
        message: "데이터 조회중",
        mode: "ios",
      });
      loading.present();

      try {
        const playerIdJson = await NeopleApi.cyPlayerId({
          nickname: encodeURI(player.value),
        });

        if (Common.isNull(playerIdJson.rows)) {
          playerId.value = "";
          playerInfo.value = null;
        } else {
          playerId.value = playerIdJson.rows[0].playerId;
          playerInfo.value = await NeopleApi.cyPlayerMatches(
            Common.preprocessing({
              playerId: playerId.value,
              gameTypeId: gameTypeId.value,
            })
          );
          gameTypeId.value = playerInfo.value.matches.gameTypeId;
        }
      } catch (error) {
        const alert = await alertController.create({
          header: "오류 발생",
          subHeader: `${error}`,
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } finally {
        loading.dismiss();
      }
    };

    const enter = (e: KeyboardEvent) => {
      if (e.key === "Enter") {
        setTimeout(() => {
          playerSearch();
        }, 250);
      }
    };

    const gamePlayType = (userCnt: number) =>
      userCnt > 0 ? `파티 ${userCnt}인` : "솔로";

    const gameKDA = (
      killCount: number,
      deathCount: number,
      assistCount: number
    ) => {
      if (deathCount > 0) {
        return Math.round(((killCount + assistCount) / deathCount) * 100) / 100;
      } else {
        return "PERFECT";
      }
    };

    const addMatches = async (next: string) => {
      const loading = await loadingController.create({
        message: "데이터 조회중",
        mode: "ios",
      });
      loading.present();

      try {
        const json = await NeopleApi.cyPlayerMatches({
          playerId: playerId.value,
          next: next,
        });
        playerInfo.value.matches.rows.push(...json.matches.rows);
        playerInfo.value.matches.next = json.matches.next;
      } catch (error) {
        const alert = await alertController.create({
          header: "오류 발생",
          subHeader: `${error}`,
          buttons: ["OK"],
          mode: "ios",
        });

        await alert.present();
      } finally {
        loading.dismiss();
      }
    };

    const gameNormalRatio = computed(() => {
      let ratio = 0;
      let game = null;

      if (!Common.isNull(playerInfo)) {
        game = (playerInfo.value.records as any[]).filter(
          (game) => game.gameTypeId === "normal"
        )[0];
      }

      if (!Common.isNull(game)) {
        if (game.winCount + game.loseCount > 0) {
          ratio =
            Math.round(
              (game.winCount / (game.winCount + game.loseCount)) * 10000
            ) / 100;
        }
      }

      return ratio;
    });

    const gameNormalRatioColor = computed(() => {
      let color = "";

      if (gameNormalRatio.value >= 75) {
        color = "primary";
      } else if (gameNormalRatio.value >= 50) {
        color = "success";
      } else if (gameNormalRatio.value >= 25) {
        color = "warning";
      } else {
        color = "danger";
      }
      return color;
    });

    const gameRatingRatio = computed(() => {
      let ratio = 0;
      let game = null;

      if (!Common.isNull(playerInfo)) {
        game = (playerInfo.value.records as any[]).filter(
          (game) => game.gameTypeId === "rating"
        )[0];
      }

      if (!Common.isNull(game)) {
        if (game.winCount + game.loseCount) {
          ratio =
            Math.round(
              (game.winCount / (game.winCount + game.loseCount)) * 10000
            ) / 100;
        }
      }

      return ratio;
    });

    const gameRatingRatioColor = computed(() => {
      let color = "";

      if (gameRatingRatio.value >= 75) {
        color = "primary";
      } else if (gameRatingRatio.value >= 50) {
        color = "success";
      } else if (gameRatingRatio.value >= 25) {
        color = "warning";
      } else {
        color = "danger";
      }
      return color;
    });

    watch(gameTypeId, (newVal, oldVal) => {
      if (!Common.isNull(oldVal)) playerSearch();
    });

    onIonViewDidLeave(() => {
      menuController.close("main-menu");
    });

    return {
      searchSharp,
      player,
      playerSearch,
      playerInfo,
      enter,
      gameNormalRatio,
      gameNormalRatioColor,
      gameRatingRatio,
      gameRatingRatioColor,
      gameTypeId,
      NeopleApi,
      gamePlayType,
      gameKDA,
      addMatches,
      Common,
    };
  },
});
